const Transition = ({ children, delay = 0 }) => {
  const { ref, inView } = useInView({
    threshold: 1,
    triggerOnce: true
  });

  return (
    <Box
      ref={ref}
      sx={{
        width: '100%',
        height: '100%'
      }}
    >
      <Box
        sx={{
          width: '100%',
          height: '100%',
          transition: [
            `opacity 1000ms ease-out ${delay}ms`,
            `transform 1000ms ease-out ${delay}ms`
          ].join(','),
          transform: `translateX(${inView ? 0 : 100}px)`,
          opacity: inView ? 1 : 0
        }}
      >
        {children}
      </Box>
    </Box>
  );
};

